<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import { EventBus } from "./js/eventBus.js";

export default {
  name: "App",
  methods: {
    jsResetMap() {
      EventBus.$emit("androidCalledResetMap");
    },
    jsLocate(lat, lng) {
      EventBus.$emit("jsLocate", lat, lng);
    },
    jsTravel() {
      EventBus.$emit("jsTravel");
    },
    jsPano() {
      EventBus.$emit("jsPano");
    },
    routeTo3d() {
      this.$router.push("MyCesium");
    },
    jsPlanning() {
      EventBus.$emit("jsPlanning");
    },
    jsSearch(keyword) {
      EventBus.$emit("jsSearch", keyword);
    },
    jsVillage() {
      EventBus.$emit("jsVillage");
    },
    jsPlotting() {
      EventBus.$emit("jsPlotting");
    },
    getPlotMapH5(plotId) {
      EventBus.$emit("getPlotMapH5",plotId);
    },
    startPlot() {
      EventBus.$emit("startPlot");
    },
    endPlot(){
      EventBus.$emit("endPlot");
    },
    cancelPlot() {
      EventBus.$emit("cancelPlot");
    }
  },
  mounted() {
    window.jsResetMap = this.jsResetMap;
    window.jsLocate = this.jsLocate;
    window.jsTravel = this.jsTravel;
    window.jsPano = this.jsPano;
    window.routeTo3d = this.routeTo3d;
    window.jsPlanning = this.jsPlanning;
    window.jsSearch = this.jsSearch;
    window.jsVillage = this.jsVillage;
    window.jsPlotting = this.jsPlotting;
    window.getPlotMapH5 = this.getPlotMapH5;
    window.startPlot = this.startPlot;
    window.endPlot = this.endPlot;
    window.cancelPlot = this.cancelPlot;
  }
};
</script>

<style>
html,
body,
#app {
  height: 100%;
  width: 100%;
  margin: 0;
}
</style>



