<template>
  <div class="cesiumContainer" id="cesiumContainer">
    <!-- 图层选择器 start -->
    <div class="layer-control-container control-container">
      <div class="layer-control-toggle toggle" @click="onLayerControlClick"></div>
      <section class="layer-control-list" v-bind:style="layerControlIsDisplay">
        <div class="layer-control-list-item" v-for="(promitive,index) in promitives" :key="index">
          <input
            class="layer-control-list-ckeckBox"
            type="checkBox"
            v-bind:checked="promitive.show"
            @change="onLayerControlItemChanged(promitive)"
          />
          <div class="layer-control-list-name">{{promitive.name}}</div>
        </div>
      </section>
    </div>
    <!-- 图层选择器 end -->

    <!-- 飞行 start -->
    <div class="fly-tool-container control-container">
      <div class="fly-tool-toggle toggle" @click="onFlyToolClick"></div>
    </div>

    <div class="fly-section-container" :style="flyPathContainerIsDisplay">
      <div class="fly-path-header" @click="onFlyPathHeaderClick">
        <div class="fly-path-header-text">飞行路径</div>
        <img :src="triIconUrl" class="fly-path-header-tri-icon" />
      </div>
      <section class="fly-path-list" :style="flyPathSectionIsDisplay">
        <div class="fly-path-list-container">
          <div
            class="fly-path-list-item"
            v-for="(flyPathItem,flyPathIndex) in flyPathArray"
            :key="flyPathIndex"
          >
            <div
              class="fly-path-list-item-name"
              @click="onFlyPathItemClicked(flyPathItem)"
            >{{flyPathItem.name}}</div>
          </div>
        </div>
      </section>
    </div>
    <!-- 飞行 end -->

    <!-- 测量 start -->
    <div class="measure-tool-container control-container">
      <div class="measure-tool-toggle toggle" @click="onMeasureToolClick"></div>
    </div>

    <div
      class="line-measure-toggle control-container toggle"
      :style="measureToolIsDisplay"
      @click="onMeasureToggleClick(0)"
    ></div>
    <div
      class="polygon-measure-toggle control-container toggle"
      :style="measureToolIsDisplay"
      @click="onMeasureToggleClick(1)"
    ></div>
    <div
      class="elevation-measure-toggle control-container toggle"
      :style="measureToolIsDisplay"
      @click="onMeasureToggleClick(2)"
    ></div>
    <div
      class="remove-measure-toggle control-container toggle"
      :style="measureToolIsDisplay"
      @click="onMeasureToggleClick(3)"
    ></div>

    <!-- 测量 end -->

    <!-- 复位 start -->
    <div class="reset-tool-container control-container">
      <div class="reset-tool-toggle toggle" @click="onResetToolClick"></div>
    </div>
    <!-- 复位 end -->
  </div>
</template>

<script>
import Cesium from "cesium/Cesium";
import "../../node_modules/cesium/Build/Cesium/Widgets/widgets.css";
import { EventBus } from "../js/eventBus.js";
import { MeasureTools } from "../js/CesiumMeasure.js";
import "../css/style.css";
import { flyPathArray } from "../js/flyPath";
import { flyWay } from "../js/myUtils.js";

export default {
  name: "MyCesium",
  data() {
    return {
      triIconUrl: "/static/tri-icon.png",
      promitives: "",
      viewer: "",
      layerControlClicked: false,
      layerControlIsDisplay: "display:none",

      flyToolClicked: false,
      flyPathContainerIsDisplay: "display:none",

      flyPathHeaderClicked: false,
      flyPathSectionIsDisplay: "display:none",
      flyPathArray: flyPathArray,

      measureToolClicked: false,
      measureToolIsDisplay: "display:none",
      measureTool: "",

      featureTooltipEntity: "", //当前显示标签的实体
      ShapeEventHandler: ""
    };
  },
  methods: {
    onLayerControlClick: function() {
      var that = this;
      if (that.layerControlClicked === false) {
        that.layerControlIsDisplay = "display:block";
        that.layerControlClicked = true;
      } else {
        that.layerControlIsDisplay = "display:none";
        that.layerControlClicked = false;
      }
    },
    onLayerControlItemChanged: function(promitive) {
      var that = this;
      if (promitive.show === true) {
        promitive.show = false;
        that.viewer.scene.requestRender();
      } else {
        promitive.show = true;
        that.viewer.scene.requestRender();
      }
    },
    onFlyToolClick: function() {
      var that = this;
      if (that.flyToolClicked === false) {
        that.flyPathContainerIsDisplay = "display:block";
        that.flyToolClicked = true;
      } else {
        that.flyPathContainerIsDisplay = "display:none";
        that.flyToolClicked = false;
      }
    },
    onFlyPathHeaderClick() {
      var that = this;
      if (that.flyPathHeaderClicked === false) {
        that.flyPathSectionIsDisplay = "display:block";
        that.flyPathHeaderClicked = true;
      } else {
        that.flyPathSectionIsDisplay = "display:none";
        that.flyPathHeaderClicked = false;
      }
    },
    onFlyPathItemClicked(flyPathItem) {
      flyWay(flyPathItem.wayArray, this.viewer);
    },
    onMeasureToolClick: function() {
      var that = this;
      if (that.measureToolClicked === false) {
        that.removeBindingInfoWindow();
        that.measureToolIsDisplay = "display:block";
        that.measureTool = new MeasureTools(that.viewer);
        that.measureToolClicked = true;
      } else {
        that.bindingInfoWindow();
        that.measureToolIsDisplay = "display:none";
        that.measureTool.destroy();
        that.measureToolClicked = false;
      }
    },
    onMeasureToggleClick: function(measureType) {
      var that = this;
      switch (measureType) {
        case 0:
          that.measureTool.destroy();
          that.measureTool.measurePolyLine();
          break;
        case 1:
          that.measureTool.destroy();
          that.measureTool.measurePolygon();
          break;
        case 2:
          that.measureTool.destroy();
          that.measureTool.measureHeight();
          break;
        case 3:
          that.measureTool.destroy();
          break;
      }
    },
    onResetToolClick: function() {
      var that = this;
      that.viewer.zoomTo(that.promitives[0]);
    },
    bindingInfoWindow: function() {
      var that = this;
      // if (ShapeEventHandler != undefined) {
      //   ShapeEventHandler.removeInputAction(
      //     Cesium.ScreenSpaceEventType.LEFT_CLICK
      //   );
      // }
      var ShapeEventHandler = new Cesium.ScreenSpaceEventHandler(
        that.viewer.scene.canvas
      );
      ShapeEventHandler.setInputAction(click => {
        var feature = that.viewer.scene.pick(click.position);
        if (feature != undefined) {
          var c3Point = that.viewer.scene.globe.pick(
            that.viewer.camera.getPickRay(click.position),
            that.viewer.scene
          );
          if (that.featureTooltipEntity === "") {
            var entity = new Cesium.Entity({
              position: c3Point,
              label: {
                text: feature.primitive.name,
                font: "30px sans-serif",
                style: Cesium.LabelStyle.FILL_AND_OUTLINE, //FILL  FILL_AND_OUTLINE OUTLINE
                fillColor: Cesium.Color.YELLOW,
                showBackground: true, //指定标签后面背景的可见性
                backgroundColor: new Cesium.Color(0.165, 0.165, 0.165, 0.8), // 背景颜色
                backgroundPadding: new Cesium.Cartesian2(6, 6), //指定以像素为单位的水平和垂直背景填充padding
                pixelOffset: new Cesium.Cartesian2(0, -200)
              }
            });
            that.viewer.entities.add(entity);
            that.featureTooltipEntity = entity;
          } else {
            that.featureTooltipEntity.entityCollection._entities._array[0]._position._value = c3Point;
            that.featureTooltipEntity.entityCollection._entities._array[0]._label._text._value =
              feature.primitive.name;
          }
          that.viewer.scene.requestRender();
        }
      }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
      that.ShapeEventHandler = ShapeEventHandler;
    },
    removeBindingInfoWindow: function() {
      var that = this;
      if (that.ShapeEventHandler !== "") {
        that.ShapeEventHandler.destroy();
      }
    }
  },
  mounted: function() {
    var that = this;
    Cesium.Ion.defaultAccessToken =
      "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiI4N2MwOWQyMy1jZmYxLTQ0MDgtODhlNy01NzJjYzg2NGJkMGYiLCJpZCI6OTk3OSwic2NvcGVzIjpbImFzciIsImdjIl0sImlhdCI6MTU1NTQwNjkxNX0.9X68vOpRUVBdbiirGXtGvKGMV1nYZFCCnKwzTjFRC1I";
    var viewer = new Cesium.Viewer("cesiumContainer", {
      // 需要进行可视化的数据源的集合
      animation: false, // 是否显示动画控件
      shouldAnimate: false,
      homeButton: false, // 是否显示Home按钮
      fullscreenButton: false, // 是否显示全屏按钮
      baseLayerPicker: false, // 是否显示图层选择控件
      geocoder: false, // 是否显示地名查找控件
      timeline: false, // 是否显示时间线控件
      sceneModePicker: false, // 是否显示投影方式控件
      navigationHelpButton: false, // 是否显示帮助信息控件
      infoBox: false, // 是否显示点击要素之后显示的信息
      requestRenderMode: true, // 启用请求渲染模式
      scene3DOnly: false, // 每个几何实例将只能以3D渲染以节省GPU内存
      sceneMode: 3, // 初始场景模式 1 2D模式 2 2D循环模式 3 3D模式  Cesium.SceneMode
      fullscreenElement: document.body // 全屏时渲染的HTML元素 暂时没发现用处
    });

    viewer._cesiumWidget._creditContainer.style.display = "none";

    this.axios
      .get("http://115.29.65.225:8082/cesiumTestData.json")
      .then(response => {
        response.data.forEach(cesiumDataObject => {
          var tileset = viewer.scene.primitives.add(
            new Cesium.Cesium3DTileset({
              url: cesiumDataObject.url,
              dynamicScreenSpaceErrorHeightFalloff: 1
            })
          );
          Object.defineProperties(tileset, {
            name: { value: cesiumDataObject.tileName }
          });
          viewer.zoomTo(tileset);
        });
      });

    viewer.terrainProvider = Cesium.createWorldTerrain({
      requestVertexNormals: true // required for terrain lighting
    });

    that.viewer = viewer;

    that.promitives = viewer.scene.primitives._primitives;

    that.bindingInfoWindow();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cesiumContainer {
  height: 100%;
  width: 100%;
}

.control-container {
  display: block;
  position: absolute;
  z-index: 1000;
}

.toggle {
  width: 24px;
  height: 24px;
  background-position: 50% 50%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.layer-control-container {
  display: none;
}

.layer-control-list {
  padding: 5px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.layer-control-list-name {
  color: black;
}

.layer-control-list-item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}

.fly-tool-container {
  top: 106px;
  right: 12px;
  background-color: white;
  offset: 1px;
  border-top-right-radius: 7px;
  border-top-left-radius: 7px;
}

.fly-tool-toggle {
  background-image: url("/static/flyTool.png");
  margin: 6px;
}

.fly-section-container {
  display: block;
  position: absolute;
  background-color: rgba(0, 0, 0, 0);
  z-index: 1000;
  top: 40px;
  left: 30%;
  right: 30%;
  width: 40%;
}

.fly-path-header {
  background-color: white;
  border: 0.5px solid gray;
  width: 100%;
  height: 30px;
  display: flex;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.fly-path-header-text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  line-height: 30px;
}

.fly-path-header-tri-icon {
  position: relative;
  left: 20px;
  width: 15px;
  height: 10px;
}

.fly-path-list-container {
  border-radius: 7px;
  background-color: white;
  width: 100%;
}

.fly-path-list-item {
  margin: 0;
  margin: auto;
  height: 30px;
}

.fly-path-list-item-name {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 30px;
  font-size: small;
}

.measure-tool-container {
  top: 142px;
  right: 12px;
  background-color: white;
  offset: 1px;
}

.measure-tool-toggle {
  background-image: url("/static/measureTool.png");
  margin: 6px;
}

.line-measure-toggle {
  background-image: url("/static/line.png");
  width: 44px;
  height: 44px;
  bottom: 176px;
  left: 12px;
}

.polygon-measure-toggle {
  background-image: url("/static/area.png");
  width: 44px;
  height: 44px;
  bottom: 130px;
  left: 12px;
}

.elevation-measure-toggle {
  background-image: url("/static/height.png");
  width: 44px;
  height: 44px;
  bottom: 84px;
  left: 12px;
}

.remove-measure-toggle {
  background-image: url("/static/clean.png");
  width: 44px;
  height: 44px;
  bottom: 38px;
  left: 12px;
}
.reset-tool-container {
  top: 178px;
  right: 12px;
  background-color: white;
  offset: 1px;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.reset-tool-toggle {
  background-image: url("/static/resetTool.png");
  margin: 6px;
}
</style>



