/*jshint esversion:6 */

import {
    divIcon,
} from "leaflet";
import Cesium from "cesium/Cesium";


/**
 *
 * 新建的divIcon
 * @param {*} iconName
 * @param {*} imgUrl
 * @returns
 */
const myDivIcon = function (iconName, imgUrl) {
    var elementIcon = divIcon({
        html:
            `
            <div class="content">` +
            iconName +
            `
            </div>
            <div class="triangle"></div>
            <img class="marker" src="` +
            imgUrl +
            `">
        `,
        iconSize: [100, 100],
        className: "class-Element"
    });
    return elementIcon;
};

function randomNum(minNum, maxNum) {
    switch (arguments.length) {
        case 1:
            return parseInt(Math.random() * minNum + 1, 10);
        case 2:
            return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
        default:
            return 0;
    }
}

const getCenterPosition = function (viewer) {
    var result = viewer.camera.pickEllipsoid(
        new Cesium.Cartesian2(
            viewer.canvas.clientWidth / 2,
            viewer.canvas.clientHeight / 2
        )
    );
    var curPosition = Cesium.Ellipsoid.WGS84.cartesianToCartographic(result);
    var lon = (curPosition.longitude * 180) / Math.PI;
    var lat = (curPosition.latitude * 180) / Math.PI;

    return {
        lon: lon,
        lat: lat
    };
};


/**
 *
 *递归遍历wayArray来进行飞行操作
 * @param {*} wayArray
 * @param {*} viewer
 */
const flyWay = function (wayArray, viewer) {
    function recur(index, arrayLength) {
        viewer.camera.flyTo({
            destination: wayArray[index],
            duration: randomNum(3, 5),
            // orientation : {
            //     heading : Math.random(),
            //     pitch : -Math.random()*0.1,
            //     roll : 0
            // },
            maximumHeight: 250,
            easingFunction: Cesium.EasingFunction.LINEAR_NONE,
            complete: function () {
                if (index < arrayLength - 1) {
                    index += 1;
                    recur(index, arrayLength);
                } else {

                }
            }
        });
    }
    recur(0, wayArray.length);
};


export {
    myDivIcon,
    getCenterPosition,
    flyWay
};



