<template>
  <div class="mapContainer" id="map">
    <div class="copy-right-content"></div>
  </div>
</template>

<script>
import {
  map,
  esri,
  tileLayerWMS,
  tileLayer,
  layerGroup,
  markerClusterGroup,
  control,
  circle,
  polygon,
  marker,
  canvas,
  popup,
  Icon,
  divIcon,
  latLng,
  CRS
} from "leaflet";

import { myDivIcon } from "../js/myUtils.js";
import { layerProviders } from "../js/layerProvider.js"; //地图基础图层
import { EventBus } from "../js/eventBus.js"; //事件载体
import "leaflet.pm";
import "leaflet.pm/dist/leaflet.pm.css";
import "../css/style.css"; //全局样式
import "leaflet-measure/dist/leaflet-measure.cn.js";
import "leaflet-measure/dist/leaflet-measure.css";
import "leaflet.markercluster/dist/MarkerCluster.css";
import "leaflet.markercluster/dist/MarkerCluster.Default.css";
import "leaflet.markercluster/dist/leaflet.markercluster.js";
import "leaflet/dist/leaflet.css";

export default {
  name: "MyMap",
  data() {
    return {
      thisMap: new Object(), //全局二维地图
      mapBounds: new Object(),
      mapZoom: "",
      isZoomIn: false,
      travelImgUrl: "/static/travelMarker.png", //地图标记点图标
      planningImgUrl: "/static/marker-icon.png",
      locatedFlag: false, //是否第一次定位的标记
      locateObject: {
        //定位的图标对象
        marker: "",
        circle: ""
      },
      remoteHost: "http://120.26.60.230:8180/charm-mmc", //远程主机地址
      panoHost: "http://vr.iwhcity.com:8000/tour/", //威海VR地址
      panoImgUrl: "/static/panoMarker.png",
      whLogoImgUrl: "/static/wh-logo.png",
      villageImgUrl: "/static/villageMarker.png",
      plotFeature: ""
    };
  },
  methods: {
    initMap: function() {
      var that = this;
      var thisMap = map(this.$el, {
        center: [37.183499, 122.262658], //122.262658,37.183499
        zoom: 9,
        // measureControl: true,
        zoomControl: false,
        attributionControl: false,
        closePopupOnClick: false
      });
      this.thisMap = thisMap; //初始化data中的thismap
      var tiandituNormalMap = tileLayer(layerProviders.TianDiTu.Normal.Map, {
        subdomains: layerProviders.TianDiTu.subdomains,
        key: layerProviders.TianDiTu.key,
        id: "tiandituNormalMap"
      });
      var tiandituNormalAnnotion = tileLayer(
        layerProviders.TianDiTu.Normal.Annotion,
        {
          subdomains: layerProviders.TianDiTu.subdomains,
          key: layerProviders.TianDiTu.key,
          id: "tiandituNormalAnnotion"
        }
      );
      var tiandituSatelliteMap = tileLayer(
        layerProviders.TianDiTu.Satellite.Map,
        {
          subdomains: layerProviders.TianDiTu.subdomains,
          key: layerProviders.TianDiTu.key,
          id: "tiandituSatelliteMap"
        }
      );
      var tiandituSatelliteAnnotion = tileLayer(
        layerProviders.TianDiTu.Satellite.Annotion,
        {
          subdomains: layerProviders.TianDiTu.subdomains,
          key: layerProviders.TianDiTu.key,
          id: "tiandituSatelliteAnnotion"
        }
      );
      var GaoDeNormalMap = tileLayer(layerProviders.GaoDe.Normal.Map, {
        subdomains: layerProviders.GaoDe.subdomains,
        id: "GaoDeNormalMap"
      });

      var GaoDeSatelliteMap = tileLayer(layerProviders.GaoDe.Satellite.Map, {
        subdomains: layerProviders.GaoDe.subdomains,
        id: "GaoDeSatelliteMap"
      });

      var GoogleNormalMap = tileLayer(layerProviders.Google.Normal.Map, {
        subdomains: layerProviders.Google.subdomains,
        id: "GoogleNormalMap"
      });

      var GoogleSatelliteMap = tileLayer(layerProviders.Google.Satellite.Map, {
        subdomains: layerProviders.Google.subdomains,
        id: "GoogleSatelliteMap"
      });

      var baseLayers = {
        天地图影像: layerGroup([
          tiandituSatelliteMap,
          tiandituSatelliteAnnotion
        ]),

        天地图地图: layerGroup([
          tiandituNormalMap,
          tiandituNormalAnnotion
        ]).addTo(thisMap)
      };

      var addonLayers = {
        高德地图: GaoDeNormalMap,
        高德影像: GaoDeSatelliteMap,
        谷歌地图: GoogleNormalMap,
        谷歌影像: GoogleSatelliteMap
      };

      var layerControl = control.layers(baseLayers, addonLayers).addTo(thisMap);
      layerControl._container.style = "position:relative;top:100px"; //更换图层选择器的默认位置
    },
    cssProblemFix: function() {
      //修复leaflet CSS的问题
      delete Icon.Default.prototype._getIconUrl;
      Icon.Default.mergeOptions({
        iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
        iconUrl: require("leaflet/dist/images/marker-icon.png"),
        shadowUrl: require("leaflet/dist/images/marker-shadow.png")
      });
    },
    resetMap: function() {
      this.thisMap.setView(
        latLng({
          lng: 122.262658,
          lat: 37.183499
        }),
        9
      );
      EventBus.$emit("resetMap"); //删除当前地图的图标组 包含全景、传统村落、旅游
      EventBus.$emit("removeMarker"); //删除规划专题下的图标
    },
    requestMarkersAndAddToMap(category) {
      var that = this;
      var TravelLayerGroup = markerClusterGroup({
        disableClusteringAtZoom: 14
      });
      this.axios
        .post(that.remoteHost + "/api/v1/poi/whPoi/list", {
          type: category
        })
        .then(function(response) {
          let resultData = response.data.data;
          for (let index = 0; index < resultData.length; index++) {
            var element = resultData[index];
            var elementIcon;
            switch (category) {
              case 3:
                elementIcon = myDivIcon(element.name, that.travelImgUrl);
                break;
              case 2:
                elementIcon = myDivIcon(element.name, that.villageImgUrl);
                break;
              case 1:
                elementIcon = myDivIcon(element.name, that.panoImgUrl);
                break;
            }
            var elementMarker = marker(
              latLng({
                lng: element.coordinateX,
                lat: element.coordinateY
              }),
              { icon: elementIcon }
            );
            TravelLayerGroup.addLayer(elementMarker);
            switch (category) {
              case 2:
                Object.defineProperties(elementMarker, {
                  //添加到对象原生的属性去
                  id: { value: element.id }
                });
                break;
              case 3:
                Object.defineProperties(elementMarker, {
                  //添加到对象原生的属性去
                  id: { value: element.id }
                });
                break;
              case 1:
                Object.defineProperties(elementMarker, {
                  //添加到对象原生的属性去
                  view_uuid: { value: element.ref2 }
                });
                break;
            }

            elementMarker.on("click", function(e) {
              that.thisMap.setView(e.latlng, 17);
              switch (category) {
                case 3:
                  android.travelPOIDetail(e.sourceTarget.id);
                  break;
                case 2:
                  android.villagePOIDetail(e.sourceTarget.id);
                  break;
                case 1:
                  android.panoPage(
                    that.panoHost + "/" + e.sourceTarget.view_uuid
                  ); //打开目标网站
                  break;
              }
            });
          } //for end
          TravelLayerGroup.addTo(that.thisMap);
          EventBus.$on("resetMap", function() {
            //注册事件用来删除当前Marker图层
            that.thisMap.removeLayer(TravelLayerGroup);
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    initEvents: function() {
      var that = this;
      EventBus.$on("androidCalledResetMap", () => {
        that.thisMap.setView(
          latLng({
            lng: 122.262658,
            lat: 37.183499
          }),
          9
        );
      });

      //乡村旅游
      EventBus.$on("jsTravel", function() {
        that.resetMap();
        that.requestMarkersAndAddToMap(3);
      });

      //传统村落
      EventBus.$on("jsVillage", function() {
        that.resetMap();
        that.requestMarkersAndAddToMap(2);
      });

      //全景威海
      EventBus.$on("jsPano", function() {
        that.resetMap();
        that.requestMarkersAndAddToMap(1);
      });

      //规划专题
      EventBus.$on("jsPlanning", function() {
        EventBus.$emit("resetMap");
        that.thisMap.setView(
          latLng({
            lng: 122.116172,
            lat: 37.469389
          }),
          12
        );
        var WMSLayer = tileLayer.wms(
          "http://ml.iwhcity.com:6080/arcgis/services/WHGH/ZTGH/MapServer/WMSServer",
          {
            layers: "0",
            format: "image/png",
            transparent: true,
            crs: CRS.EPSG4326,
            opacity: 0.5
          }
        );
        WMSLayer.addTo(that.thisMap);
        WMSLayer.bringToFront();

        that.thisMap.on("click", function(parms) {
          EventBus.$emit("removeMarker");
          that.axios //构造identify请求
            .get(
              "http://ml.iwhcity.com:6080/arcgis/rest/services/WHGH/ZTGH/MapServer/identify",
              {
                params: {
                  geometry: parms.latlng.lng + "," + parms.latlng.lat,
                  geometryType: "esriGeometryPoint",
                  sr: "EPSG:4326",
                  layers: "all",
                  tolerance: "0",
                  geometryPrecision: "10",
                  f: "json",
                  mapExtent: "122.14,37.3",
                  imageDisplay: "600,550,96"
                }
              }
            )
            .then(function(response) {
              var element = response.data.results[0];

              var elementIcon = myDivIcon(
                element.attributes.USES,
                that.planningImgUrl
              );
              var elementMarker = marker(parms.latlng, {
                icon: elementIcon
              }).addTo(that.thisMap);
              that.thisMap.setView(parms.latlng, 16);
              EventBus.$on("removeMarker", function() {
                that.thisMap.removeLayer(elementMarker);
              });
            })
            .catch(function(error) {
              console.log(error);
            });
        });

        EventBus.$on("resetMap", function() {
          that.thisMap.removeLayer(WMSLayer);
        });
      });

      //定位功能
      EventBus.$on("jsLocate", function(lat, lng) {
        var latlng = latLng({
          lng: lng,
          lat: lat
        });
        if (!that.locatedFlag) {
          var radius = 16;
          that.locateObject.marker = marker(latlng).addTo(that.thisMap);
          that.locateObject.circle = circle(latlng, radius).addTo(that.thisMap);
          that.thisMap.setView(latlng, 17);
          that.locatedFlag = true;
        } else {
          that.locateObject.marker.setLatLng(latlng);
          that.locateObject.circle.setLatLng(latlng);
          that.thisMap.setView(latlng, 16);
        }
      });

      //搜索功能
      EventBus.$on("jsSearch", function(keyword) {
        that.resetMap();
        var resultLayerGroup = markerClusterGroup({
          disableClusteringAtZoom: 13
        });
        this.axios
          .post(that.remoteHost + "/api/v1/poi/whPoi/list", { name: keyword })
          .then(function(response) {
            let resultData = response.data.data;
            for (let index = 0; index < resultData.length; index++) {
              var element = resultData[index];
              var elementIcon;

              if (element.type === "3") {
                elementIcon = myDivIcon(element.name, that.travelImgUrl);
              } else if (element.type === "1") {
                elementIcon = myDivIcon(element.name, that.panoImgUrl);
              } else {
                elementIcon = myDivIcon(element.name, that.villageImgUrl);
              }

              var elementMarker = marker(
                latLng({
                  lng: element.coordinateX,
                  lat: element.coordinateY
                }),
                { icon: elementIcon }
              );
              resultLayerGroup.addLayer(elementMarker);
              Object.defineProperties(elementMarker, {
                //添加到对象原生的属性去
                id: { value: element.id },
                type: { value: element.type }
              });
              elementMarker.on("click", function(e) {
                that.thisMap.setView(e.latlng, 17);
                android.searchResultDetail(
                  e.sourceTarget.id,
                  e.sourceTarget.type
                );
              });
            } //for end
            resultLayerGroup.addTo(that.thisMap);
            EventBus.$on("resetMap", function() {
              //注册事件用来删除当前Marker图层
              that.thisMap.removeLayer(resultLayerGroup);
            });
          })
          .catch(function(error) {
            console.log(error);
          });
      });

      //标绘功能
      EventBus.$on("jsPlotting", function() {
        that.resetMap();
        var token = android.getToken();
        // var token = "596b3f19ca074f68b7bb95c14701325a";
        //地图上显示数据库已有标绘
        this.axios
          .post(that.remoteHost + "/api/v1/marker/whMarkerInf/list", {
            userId: token
          })
          .then(function(response) {
            var plots = response.data.data;

            for (let index = 0; index < plots.length; index++) {
              const element = plots[index];
              var coord = element.coordinates;
              // var coord = `[{"lat":36.96086580957587,"lng":122.22839355468751}
              // ,{"lat":37.142803443716836,"lng":121.97296142578126}
              // ,{"lat":37.302460074782296,"lng":122.51678466796876}
              // ,{"lat":37.03106210011894,"lng":122.59643554687501}
              // ,{"lat":36.8510544475565,"lng":122.45361328125}]`;
              var coordObj = JSON.parse(coord);
              var lines = new polygon(coordObj).addTo(that.thisMap);

              lines.on("click", () => {
                android.plotDetail(element.id);
              });
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      });

      //开始标绘
      EventBus.$on("startPlot", function() {
        that.thisMap.pm.addControls({
          position: "topleft",
          drawCircle: false,
          drawMarker: false,
          drawPolyline: false,
          drawRectangle: false,
          editMode: false,
          dragMode: false,
          cutPolygon: false
        });

        that.thisMap.on("pm:create", e => {
          if (that.plotFeature !== "") {
            //只能画一个 不然就移除
            that.plotFeature.layer.remove();
          }
          // var result = JSON.stringify(e.layer._latlngs[0]);
          that.plotFeature = e;
        });
      });

      //结束标绘
      EventBus.$on("endPlot", function() {
        android.addNewPlot(JSON.stringify(that.plotFeature.layer._latlngs[0]));
      });

      //单个标绘地图展示
      EventBus.$on("getPlotMapH5", function(plotId) {
        // var token = android.getToken();
        // var token = "596b3f19ca074f68b7bb95c14701325a";
        that.axios
          .post(that.remoteHost + "/api/v1/marker/whMarkerInf/get/" + plotId)
          .then(response => {
            const element = response.data.data;
            var coord = element.coordinates;
            var coordObj = JSON.parse(coord);
            var lines = new polygon(coordObj).addTo(that.thisMap);
            that.thisMap.fitBounds(lines._bounds);
          });
      });
    }
  },

  mounted: function() {
    this.initMap();
    this.initEvents();
    this.cssProblemFix();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mapContainer {
  height: 100%;
  width: 100%;
}

.copy-right-content {
  position: absolute;
  left: 20px;
  bottom: 20px;
  z-index: 1999;
  width: 60px;
  height: 60px;
  background-image: url("/static/wh-logo.png");
  background-repeat: space;
  background-size: cover;
}

/* layerControl 自定义样式 */

>>> .leaflet-control-layers-toggle {
  background-image: url("/static/layersControl.png") !important;
  width: 30px !important;
  height: 30px !important;
}

>>> .leaflet-retina .leaflet-control-layers-toggle {
  background-image: url("/static/layersControl.png") !important;
  width: 30px !important;
  height: 30px !important;
}
</style>



