import Vue from 'vue'
import Router from 'vue-router'
import MyMap from '@/components/MyMap'
import MyCesium from '@/components/MyCesium'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MyMap',
      component: MyMap
    },
    {
      path: '/MyCesium',
      name: 'MyCesium',
      component: MyCesium
    }
  ]
})



